import React from 'react';
import { ApolloProvider} from '@apollo/react-hooks';
import ApolloClient, {gql} from 'apollo-boost';
import { useQuery } from '@apollo/react-hooks';
import moment from 'moment';
import {
  //BrowserRouter
  HashRouter as Router,
  Switch,
  Route,
} from 'react-router-dom';

// Views
import Tables from './views/tables/tables'
import Trades from './views/traders/traders'
import LinkList from './views/tables/LinkList'
import ATables from './views/tables/atables'

const client = new ApolloClient({
  uri: 'https://auto-alpha-db.herokuapp.com/v1/graphql',
});

const ORDERS_QUERY = gql`
  {
    orders(where: {trade_id: {_eq: 266}}, order_by: {trade_date: asc, time: asc}) {
      id
      strike
      time
      trade_date
      call_put
      action
      exp_date
      price
      is_open
    }
  }
`;

const INTRA_DAY_PRICE = (strike, call_put, dt_init, dt_end) => { return gql`
  {
    intraday_price_action(where: {strike: {_eq: "${strike}"}, call_put: {_eq: "${call_put}"}, date_time: {_gte: "${dt_init}"}, _and: {date_time: {_lte: "${dt_end}"}}}, order_by: {trade_date: asc, time: asc}) {
      trade_date
      time
      call_put
      exp_date
      close
    }
  }
`};

function App() {
  return (
    <ApolloProvider client={client}>
      <div>
        <Router>
          <Switch>
            <Route exact path="/" component={Trades}></Route>
            <Route path="/orders/:trade_id" component={ATables}></Route>
            <Route path="/tables" component={Tables}></Route>
            <Route path="/about" component={About}></Route>
            <Route path="/about2" component={About2}></Route>
            <Route path="/welcome" component={Welcome}></Route>
            <Route path="/links" component={LinkList}></Route>
          </Switch>
        </Router>
      </div>
    </ApolloProvider>
  )
}

function About() {
  const { loading, error, data } = useQuery(ORDERS_QUERY);

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error :(</p>;
  console.log(data);
  var fecha1 = moment('20/06/2020 12:00:00', 'DD/MM/YYYY HH:mm:ss');
  var fecha2 = moment('25/06/2020 12:00:00', 'DD/MM/YYYY HH:mm:ss');
  console.log(fecha2.diff(fecha1, 'minutes'));
  return <h2>About</h2>;
}

function Welcome() {
  client
    .query({
      query: gql`
        {
          orders(where: {trade_id: {_eq: 266}}, order_by: {trade_date: asc, time: asc}) {
            id
            strike
            time
            trade_date
            call_put
            action
            exp_date
            price
            is_open
          }
        }
      `
    })
    .then(result => console.log(result))
    .catch(err => console.error(err));
  return <h2>Welcome</h2>;
}

function About2() {
  const { loading, error, data } = useQuery(INTRA_DAY_PRICE('700', 'C', '2020-01-30T15:43:08Z', '2020-01-30T15:49:08Z'));

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error :(</p>;
    console.log('Data 2', data);
  return <h2>About2</h2>;
}

export default App;
