import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import {gql} from 'apollo-boost';
import { useQuery, useApolloClient } from '@apollo/react-hooks';
import BootstrapTable from 'react-bootstrap-table-next';
import { Query } from 'react-apollo'
import moment from 'moment';
import {Line} from 'react-chartjs-2';

import './tables.css';

function ATables() {

	const [state, setState] = useState({
		orders:[],
		columns: [
	      		{
				  dataField: 'id',
				  text: 'ID'
				},
		      	{
				  dataField: 'symbol',
				  text: 'Symbol'
				}, 
				{
				  dataField: 'strike',
				  text: 'Strike'
				}, 
				{
				  dataField: 'exp_date',
				  text: 'Exp Date'
				},
				{
				  dataField: 'trade_date',
				  text: 'Date'
				},
				{
				  dataField: 'time',
				  text: 'Time'
				},
				{
				  dataField: 'price',
				  text: 'Price'
				},
				{
				  dataField: 'call_put',
				  text: 'Call/Put'
				},
				{
				  dataField: 'quantity',
				  text: 'Quantity'
				},
				{
				  dataField: 'value',
				  text: 'Value'
				},
				{
				  dataField: 'current_pnl',
				  text: 'Current PnL'
				}
			],
		groups:[],
		dataGroups: {},
		filldata: {},
		iPrices:[]
	});

	let { trade_id } = useParams();
	console.log('PARAM', trade_id)
	const client = useApolloClient();
	let groups_ready = [], dtimes_process = [], dt_orders_process = [];
	sessionStorage.setItem('groups_ready', JSON.stringify(groups_ready));
	sessionStorage.setItem('dtimes_process', JSON.stringify(dtimes_process));
	sessionStorage.setItem('dt_orders_process', JSON.stringify(dt_orders_process));

	let trad_info = JSON.parse(sessionStorage.getItem('trad_info'));

	useEffect(() => {
		client
		    .query({
		      query: INTRA_DAY_PRICE('700', 'C', '2020-01-30T15:43:08Z', '2020-01-30T15:49:08Z')
		    })
		    .then(result => {
		    	state.iPrices = result.data.intraday_price_action;
		    	//console.log('Prices Client', state.iPrices)
		    })
		    .catch(err => console.error(err))
	});

	return (
        <Query query={ORDERS_QUERY(trade_id)}>
	        {({ loading, error, data }) => {
	          	if (loading) return <div>Buscando data</div>
	          	if (error) return <div>Ha ocurrido un error: {error}</div>
	          	if (data) {
	          		let dtimes_process = sessionStorage.getItem('dtimes_process') && JSON.parse(sessionStorage.getItem('dtimes_process')).length ? JSON.parse(sessionStorage.getItem('dtimes_process')) : [];
	          		let dt_orders_process = sessionStorage.getItem('dt_orders_process') && JSON.parse(sessionStorage.getItem('dt_orders_process')).length ? JSON.parse(sessionStorage.getItem('dt_orders_process')) : [];;
	          		sessionStorage.setItem('dtimes_process', JSON.stringify(dtimes_process));
	          		sessionStorage.setItem('dt_orders_process', JSON.stringify(dt_orders_process));
	          		/*const ordersToRender = data.orders
		          	ordersToRender.map( order => state.orders.push(order));*/
		          	state.orders = data.orders;

		          	let nlength = data.orders.length;
		          	for (let i = 0; i < nlength; i++) {
		          		//console.log('Orders n#: ',i+1);
		          		let _aux = state.orders[i].strike + state.orders[i].exp_date + state.orders[i].call_put;
		          		if (!state.groups.includes(_aux)) {
		          			state.groups.push(_aux)
		          		} 
		          		/*else {
		          			if (!state.dataGroups.hasOwnProperty(_aux)) {
		          				state.dataGroups[_aux] = [];
		          			} else{
		          				state.dataGroups[_aux].push(state.orders[i])
		          			}
		          		}*/
		          		if (!state.dataGroups.hasOwnProperty(_aux)) {
		          				state.dataGroups[_aux] = [];
		          		}
						state.dataGroups[_aux].push(state.orders[i])
		          	}
		          	// Rellenando los minutos
		          	// recorro los grupos disponibles
		          	let ngroups = state.groups.length;
		          	sessionStorage.setItem('dt_groups_process', ngroups);
		          	//console.log('Cantidad de groups:', ngroups)
		          	for (let i = 0; i < ngroups; i++) {
		          		let dataGroup = state.dataGroups[state.groups[i]];
		          		state.filldata[state.groups[i]] = [];
		          		if (dataGroup.length) {
		          			let fecha1 = moment(dataGroup[0]['trade_date'] + ' ' + dataGroup[0]['time'], 'DD-MM-YYYY HH:mm:ss');
							let fecha2 = moment(dataGroup[ngroups-1]['trade_date'] + ' ' + dataGroup[ngroups-1]['time'], 'DD-MM-YYYY HH:mm:ss');
							let diferencia = fecha2.diff(fecha1, 'minutes');
							//console.log('diferencia', diferencia);
							for (let j = 1; j < diferencia; j++) {
								let new_time = moment(dataGroup[0].time, 'h:m:s').add(j, 'minutes').format('HH:mm:ss');
								let fillable = {
									id: dataGroup[0].id + '.' + j,
									strike: dataGroup[0].strike,
									action: dataGroup[0].action,
									call_put: dataGroup[0].call_put,
									exp_date: dataGroup[0].exp_date,
									price: '',									
									is_open: dataGroup[0].is_open,
									trade_date: dataGroup[0].trade_date,
									time: new_time,
									quantity: 0
								}
								state.filldata[state.groups[i]].push(fillable);
							}
		          		}
		          	}
		          	// A concatenar groups y fillables
		          	for (let i = 0; i < ngroups; i++) {
		          		let concat = state.dataGroups[state.groups[i]].concat(state.filldata[state.groups[i]]);
		          		concat.sort(function(a,b){
						  return new Date(a.trade_date + ' ' + a.time) - new Date(b.trade_date + ' ' + b.time);
						});
		          		state.dataGroups[state.groups[i]] = concat;
		          	}
		          	//console.log('Groups: ',state.groups);
		          	//console.log('dataGroups',state.dataGroups);
		          	//console.log('FillData', state.filldata);
		          	if (state.iPrices.length) {
		          		return (
				            <div className="container">
				              {state.groups.map( (group) => <BootstrapTable keyField={group} data={ state.dataGroups[group] } columns={ state.columns } />)}
				            </div>
				        )
		          	} else {
		          		return (
		          			<div className="container">
		          				<ul>
		          					<li keyField="trade_id">ID: {trade_id}</li>
		          					<li keyField="return_dollar">Return Dollar: {trad_info.return_dollar}</li>
		          					<li keyField="return_percentage">Return Percentage: {trad_info.return_percentage}</li>
		          					<li keyField="id_reference">Reference ID: {trad_info.id_reference}</li>
		          				</ul>
		          				{state.groups.map( (group, index) => <Tprice name={group} indx={index} orders={ state.dataGroups[group] } />)}
		          			</div>
		          		)
		          	}
	          	}
	        }}
	    </Query>
    )
}

function Tprice(props) {
	//console.log('Props',props.orders);
	const [state, setState] = useState({
		xcolumns: [
	      		{
				  dataField: 'id',
				  text: 'ID'
				},
		      	{
				  dataField: 'symbol',
				  text: 'Symbol'
				}, 
				{
				  dataField: 'strike',
				  text: 'Strike'
				}, 
				{
				  dataField: 'exp_date',
				  text: 'Exp Date'
				},
				{
				  dataField: 'trade_date',
				  text: 'Date'
				},
				{
				  dataField: 'time',
				  text: 'Time'
				},
				{
				  dataField: 'price',
				  text: 'Price'
				},
				{
				  dataField: 'call_put',
				  text: 'Call/Put'
				},
				{
				  dataField: 'quantity',
				  text: 'Quantity'
				},
				{
				  dataField: 'position',
				  text: 'Position'
				},
				{
				  dataField: 'value',
				  text: 'Value'
				},
				{
				  dataField: 'accum_value',
				  text: 'Accum Value'
				},
				{
				  dataField: 'current_pnl',
				  text: 'Current PnL'
				}
			],
		xorders: []
	});
	
	let groups_ready = sessionStorage.getItem('groups_ready') ? JSON.parse(sessionStorage.getItem('groups_ready')) : [];
	if (!groups_ready.includes(props.name)) {
		groups_ready.push(props.name);
	}
	sessionStorage.setItem('groups_ready', JSON.stringify(groups_ready));
	let dt_price = props.orders[0];
	//console.log('VERIFICANDO', dt_price)
	let orderslength = props.orders.length;
	let ultimo = props.orders[orderslength - 1];
	let cp = dt_price.call_put === 'CALL' ? 'C' : 'P';
	let fecha_desde = dt_price.trade_date + ' ' + dt_price.time;
	let fecha_hasta = dt_price.trade_date + ' ' + ultimo.time;
	const { loading, error, data } = useQuery(INTRA_DAY_PRICE(dt_price.strike, cp, fecha_desde, fecha_hasta));
	if (loading) return <p>Loading...</p>;
	if (error) return <p>Error :(</p>;
	if (data) {
		//console.log('Intra day price: ', data)
		// Iteramos para buscar precio
		let intradays = data.intraday_price_action;
		for (let i = 0; i < orderslength; i++) {
			if (props.orders[i].price === '') {
				let _aux_time = props.orders[i].time.substring(5,0) + ':00';
				let dt_orders = props.orders[i].trade_date + ' ' + _aux_time;
				let intra_length = intradays.length;
				for (let j = 0; j < intra_length; j++) {
					let dt_intra = intradays[j].trade_date + ' ' + intradays[j].time;
					if (dt_orders == dt_intra) {
						props.orders[i].price = intradays[j].close;
						break;
					}
				}
			}
		}
		// Obtenemos lo que hay en sessionStorage
		let dtimes_process = JSON.parse(sessionStorage.getItem('dtimes_process'));
		let dt_orders_process = JSON.parse(sessionStorage.getItem('dt_orders_process'));
		console.log('DTO',dt_orders_process)
		let dt_groups_process = sessionStorage.getItem('dt_groups_process');
		let current_pnl = 0;
		let position = 0;
		let accum_value = 0;
		let accum_ant = 0;
		for(let order in props.orders) {
			if (!state.xorders.includes(props.orders[order])) {
				// Logic quantity
				if (props.orders[order].action == 'Sell') {
					props.orders[order].quantity = props.orders[order].quantity * -1; 
				}
				// Calculamos valor
				let value = props.orders[order].price * props.orders[order].quantity;
				props.orders[order].value = value;
				// Accum Value
				accum_value = accum_value + props.orders[order].value;
				props.orders[order].accum_value = accum_value.toFixed(2);
				// Position calculate
				position = position + props.orders[order].quantity;
				props.orders[order].position = position;
				// current_pnl
				//current_pnl = current_pnl + value;
				let c_pnl = (props.orders[order].price * props.orders[order].position) - props.orders[order].accum_value;
				props.orders[order].current_pnl = c_pnl.toFixed(2);
				//console.log('Order', props.orders[order])
				state.xorders.push(props.orders[order])
				// Preparando para insertar data en sessionStorage
				let date_process = props.orders[order].trade_date + ' ' + props.orders[order].time.substring(5,0);
				if (!dtimes_process.includes(date_process)) {
					dtimes_process.push(date_process);
				}
				if (!dt_orders_process.some(e => e.id == props.orders[order].id)) {
					dt_orders_process.push(props.orders[order]);
				}
			}
		}
		sessionStorage.setItem('dtimes_process', JSON.stringify(dtimes_process));
		sessionStorage.setItem('dt_orders_process', JSON.stringify(dt_orders_process));
		//console.log('INDICE', props.indx)
		return (
			<div className="container">
				<h4>Leg {props.indx + 1}</h4>
              	<BootstrapTable keyField={props.name} data={ state.xorders } columns={ state.xcolumns } />
              	<TResumen current={props.name} ></TResumen>
            </div>
		)
	}
}

function TResumen(props) {

	const [state, setState] = useState({
		ycolumns: [
	      		{
				  dataField: 'date',
				  text: 'Date'
				},
		      	{
				  dataField: 'time',
				  text: 'Time'
				}, 
				{
				  dataField: 'running_pnl',
				  text: 'Running PnL'
				}
			],
		xrows: [],
		labels: [],
		datasets: [
		    {
		      label: 'Running PnL',
		      fill: true,
		      lineTension: 0,
		      backgroundColor: 'rgba(0,128,0,0.5)',
		      borderColor: 'rgba(0,0,0,1)',
		      borderWidth: 1,
		      data: []
		    }
		  ]
	});

	let showTable = false;
	let groups_ready = JSON.parse(sessionStorage.getItem('groups_ready'));
	let ngroups = sessionStorage.getItem('dt_groups_process');
	let pos = groups_ready.indexOf(props.current);
	console.log(props.current, pos);
	if (pos == (groups_ready.length - 1)) {
		//console.log('A mostrar table');
		showTable = true;
	}
	if (showTable) {
		// A preparar data para info
		// todas las fechas y minutos
		let all_dt = JSON.parse(sessionStorage.getItem('dtimes_process'));
		// Todas las ordenes procesadas
		let all_orders = JSON.parse(sessionStorage.getItem('dt_orders_process'));
		// Para el grafico
		state.labels = all_dt;

		let n_dt = all_dt.length;
		for (let i = 0; i < n_dt; i++) {
			let n_ao = all_orders.length;
			let running_pnl = 0;
			for(let j = 0; j < n_ao; j++) {
				let fecha_compare = all_orders[j].trade_date + ' ' + all_orders[j].time.substring(5,0);
				if (fecha_compare == all_dt[i]) {
					running_pnl = running_pnl + parseFloat(all_orders[j].current_pnl);
				}
			}
			let splited = all_dt[i].split(' ');
			let obj = {
				'date': splited[0],
				'time': splited[1],
				'running_pnl': running_pnl.toFixed(2) * 100
			}
			state.xrows.push(obj);
			state.datasets[0].data.push(obj.running_pnl);
		}
		return (
			<div className="row">
				<div className="col-md-12">
					<h4>Running PnL Table Form</h4>
					<BootstrapTable keyField={props.name + 'r'} data={ state.xrows } columns={ state.ycolumns } />
				</div>
				<div className="col-md-12">
					<div className="row">
				        <Line
				          data={state}
				          options={{
				            title:{
				              display:true,
				              text:'Running PnL Chart',
				              fontSize:20
				            },
				            legend:{
				              display:true,
				              position:'bottom'
				            }
				          }}
				        />
			      	</div>
				</div>
			</div>
		)
	} else {
		return (
			<div className="row">
				<div className="col-md-12">
					<hr/>
				</div>
			</div>
		)
	}
}

const ORDERS_QUERY = (trade_id) => {
	return gql`
	  {
	    orders(where: {trade_id: {_eq: ${trade_id}}}, order_by: {trade_date: asc, time: asc}) {
	      	id
	      	strike
	     	time
	      	trade_date
	      	call_put
	      	action
	      	exp_date
	     	price
	      	is_open,
	    	quantity
	    }
	  }
	`
};

const INTRA_DAY_PRICE = (strike, call_put, dt_init, dt_end) => { 
	return gql`
	  {
	    intraday_price_action(where: {strike: {_eq: "${strike}"}, call_put: {_eq: "${call_put}"}, date_time: {_gte: "${dt_init}"}, _and: {date_time: {_lte: "${dt_end}"}}}, order_by: {trade_date: asc, time: asc}) {
	      trade_date
	      time
	      call_put
	      exp_date
	      close
	    }
	  }
	`
};

export default ATables;