import React from 'react';
import BootstrapTable from 'react-bootstrap-table-next';
import {gql} from 'apollo-boost';
import { Query } from 'react-apollo'
//import { useQuery, useLazyQuery, useApolloClient } from '@apollo/react-hooks';

class Tables extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			componentMounted: false,
	      	columns: [
	      		{
				  dataField: 'id',
				  text: 'ID'
				},
		      	{
				  dataField: 'symbol',
				  text: 'Symbol'
				}, 
				{
				  dataField: 'strike',
				  text: 'Strike'
				}, 
				{
				  dataField: 'exp_date',
				  text: 'Exp Date'
				},
				{
				  dataField: 'trade_date',
				  text: 'Date'
				},
				{
				  dataField: 'time',
				  text: 'Time'
				},
				{
				  dataField: 'price',
				  text: 'Price'
				},
				{
				  dataField: 'quantity',
				  text: 'Quantity'
				},
				{
				  dataField: 'value',
				  text: 'Value'
				},
				{
				  dataField: 'current_pnl',
				  text: 'Current PnL'
				}
			],
			products: [
			]
		}
	}

	componentDidMount() {
	    this.state.componentMounted = true;
	    console.log(this.state.componentMounted);
	}

	componentWillUnmount() {
	    console.log('Chao')
  	}
  	UNSAFE_componentWillUpdate() {
  		console.log('LLegue aqui')
  	}

	render() {
	    return (
	      	<Query query={FEED_QUERY}>
	        {({ loading, error, data }) => {
	          	if (loading) return <div>Buscando data</div>
	          	if (error) return <div>Ha ocurrido un error: {error}</div>
	          	if (data) {
		          	/*ordersToRender.map( order => this.state.products.push(order));
		          	console.log('Products', this.state.products)*/
		          	this.state.products = data.orders;
		          	return (
		            <div className="container">
		              <BootstrapTable keyField='id' data={ this.state.products } columns={ this.state.columns } />
		            </div>
		          )
	          	}
	        }}
	      </Query>
	    )
	}
}

const FEED_QUERY = gql`
  {
    orders(where: {trade_id: {_eq: 266}}, order_by: {trade_date: asc, time: asc}) {
      id
      strike
      time
      trade_date
      call_put
      action
      exp_date
      price
      is_open
    }
  }
`;
export default Tables;