import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useParams } from "react-router-dom";
import {gql} from 'apollo-boost';
import { useQuery, useApolloClient } from '@apollo/react-hooks';
import BootstrapTable from 'react-bootstrap-table-next';
import { Query } from 'react-apollo'
import moment from 'moment';
import {Line} from 'react-chartjs-2';

import './trades.css';

function Trades() {

	const [state, setState] = useState({
		trows:[],
		columns: [
	      		{
				  dataField: 'id',
				  text: 'ID'
				},
		      	{
				  dataField: 'id_reference',
				  text: 'Reference ID'
				}, 
				{
				  dataField: 'symbol_display',
				  text: 'Symbol'
				}, 
				{
				  dataField: 'return_dollar',
				  text: 'Return Dollar'
				},
				{
				  dataField: 'return_percentage',
				  text: 'Return Percentage'
				},
				{
				  dataField: 'open_date',
				  text: 'Open Date'
				},
				{
				  dataField: 'close_date',
				  text: 'Close Date'
				},
			],
	});

	const history = useHistory();

	const rowEvents = {
		onClick: (e, row, rowIndex) => {
			console.log(`enter on row with index:`, row);
			let obj = {
				return_dollar: row.return_dollar,
				id_reference: row.id_reference,
				return_percentage: row.return_percentage
			}
			sessionStorage.setItem('trad_info',JSON.stringify(obj));
			history.push("/orders/" + row.id);
	  	}
	};

	useEffect(() => {
	});

	return (
        <Query query={TRADES_QUERY()}>
	        {({ loading, error, data }) => {
	          	if (loading) return <div>Buscando data</div>
	          	if (error) return <div>Ha ocurrido un error: {error}</div>
	          	if (data) {
	          		console.log(data);
	          		let trades = data.trades;
	          		let ntrades = trades.length;
	          		console.log('Ntrades', ntrades);
	          		for (var i = 0; i < ntrades; i++) {
	          			trades[i]['symbol_display'] = trades[i].symbol.symbol
	          			state.trows.push(trades[i])
	          		}
	          		return (
	          			<div className="container">
	          				<h4>Trades List</h4>
	          				<BootstrapTable keyField={'trade_tbl'} data={ state.trows } columns={ state.columns } rowEvents={ rowEvents }/>
	          			</div>
	          		)
	          	}
	        }}
	    </Query>
    )
}

const TRADES_QUERY = () => {
	return gql`
	  {
	    trades {
	    	id
	      	id_reference
	      	symbol {
		      symbol
		    }
	      	return_dollar
	      	return_percentage
	      	open_date
    		close_date
	    }
	  }
	`
};

export default Trades;